
.chat-agent-card {
  background: white;
  border-radius: 8px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  // 禁用文本选择，避免hover时的选中效果
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  
  .card-avatar {
    position: relative;
    width: 48px;
    height: 48px;
    margin: 0 auto;
    
    .status-indicator {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      border: 2px solid white;
      
      &.status-online {
        background: #52c41a;
        animation: pulse 2s infinite;
      }
      
      &.status-offline {
        background: #d9d9d9;
      }
      
      &.status-inactive {
        background: #ff4d4f;
      }
    }
  }
  
  .card-info {
    flex: 1;
    
    .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;
      
      .agent-name {
        margin: 0;
        font-size: 15px;
        font-weight: 600;
        color: #303133;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      
      .agent-badges {
        display: flex;
        gap: 4px;
      }
    }
    
    .agent-description {
      color: #606266;
      font-size: 13px;
      line-height: 1.5;
      margin: 0 0 12px 0;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    
    .agent-meta {
      display: flex;
      gap: 12px;
      margin-bottom: 8px;
      
      .meta-item {
        display: flex;
        align-items: center;
        gap: 4px;
        font-size: 12px;
        color: #909399;
        
        i {
          font-size: 14px;
        }
      }
    }
    
    .agent-capabilities {
      display: flex;
      flex-wrap: wrap;
      gap: 4px;
    }
  }
  
  .card-actions {
    .el-button {
      width: 100%;
      // 防止自动聚焦
      &:focus {
        outline: none;
      }
    }
  }
  
  // 防止描述文本被选中
  .agent-description {
    cursor: default;
  }
  
  // 确保所有文本元素不可选择
  * {
    user-select: none;
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}
