
.chat-page {
  height: 100% !important;
  background-color: #f1f1f1;
  position: relative;

  // 重写Element UI的el-container样式
  &.el-container {
    height: 100% !important;
    flex-direction: row;
  }

  // 🆕 文件拖拽样式
  &.file-drag-over {
    background: rgba(0, 122, 255, 0.1) !important;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 3px dashed #007AFF;
      border-radius: 8px;
      pointer-events: none;
      z-index: 999;
    }
  }

  .chat-box {
    flex: 1;
    height: 100% !important;
    display: flex;
    flex-direction: column;
    min-height: 0; // 添加这行，确保flex子项能正确收缩

    // 重写Element UI的el-container样式
    &.el-container {
      height: 100% !important;
      flex-direction: column;
    }
    
    // 容器管理组件包装器
    .container-manager-wrapper {
      flex-shrink: 0;
      z-index: 10;
    }
  }

  .thinking-panel {
    height: 100% !important;
    border-left: 1px solid #e8eaec;
    background: white;

    // 重写Element UI的el-aside样式
    &.el-aside {
      height: 100% !important;
    }

    .panel-tabs {
      height: 100%;
      display: flex;
      flex-direction: column;

      .tabs-header {
        display: flex;
        border-bottom: 1px solid #e8eaec;
        background: #f8f9fa;

        .tab-btn {
          flex: 1;
          padding: 12px 8px;
          border: none;
          background: transparent;
          cursor: pointer;
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 4px;
          transition: all 0.2s;

          &:hover {
            background: #e9ecef;
          }

          &.active {
            background: white;
            border-bottom: 2px solid #007bff;
            color: #007bff;
          }

          .tab-icon {
            font-size: 16px;
          }

          .tab-label {
            font-size: 11px;
            font-weight: 500;
          }
        }
      }

      .tabs-content {
        flex: 1;
        overflow: hidden;

        .tab-panel {
          height: 100%;
          overflow-y: auto;
        }
      }
    }
  }

  .panel-toggle {
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
    background: #007bff;
    color: white;
    padding: 8px 12px;
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
    transition: all 0.3s ease;
    z-index: 100;

    &:hover {
      background: #0056b3;
      transform: translateY(-50%) scale(1.05);
    }

    .toggle-icon {
      margin-right: 6px;
    }

    .toggle-text {
      font-size: 12px;
      font-weight: 500;
    }
  }
}

// AI助手面板样式
.ai-assistant-panel {
  padding: 16px;
  height: 100%;
  overflow-y: auto;

  .assistant-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    h3 {
      margin: 0;
      font-size: 16px;
      font-weight: 600;
      color: #2c3e50;
    }

    .assistant-status {
      font-size: 12px;
      padding: 4px 8px;
      border-radius: 12px;
      font-weight: 500;

      &.active {
        background: #d4edda;
        color: #155724;
      }

      &.idle {
        background: #fff3cd;
        color: #856404;
      }

      &.analyzing {
        background: #cce5ff;
        color: #004085;
      }
    }
  }

  .quick-actions {
    margin-bottom: 24px;

    h4 {
      margin: 0 0 12px 0;
      font-size: 14px;
      font-weight: 600;
      color: #495057;
    }

    .action-buttons {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 8px;

      .action-btn {
        padding: 8px 12px;
        border: 1px solid #dee2e6;
        border-radius: 6px;
        background: white;
        color: #495057;
        cursor: pointer;
        font-size: 12px;
        text-align: center;
        transition: all 0.2s;

        &:hover {
          background: #f8f9fa;
          border-color: #adb5bd;
        }
      }
    }
  }

  .ai-insights {
    margin-bottom: 24px;

    h4 {
      margin: 0 0 12px 0;
      font-size: 14px;
      font-weight: 600;
      color: #495057;
    }

    .insights-list {
      .insight-item {
        display: flex;
        gap: 12px;
        padding: 12px;
        margin-bottom: 8px;
        background: #f8f9fa;
        border-radius: 8px;
        border-left: 3px solid #dee2e6;

        &.priority-high {
          border-left-color: #dc3545;
        }

        &.priority-medium {
          border-left-color: #ffc107;
        }

        &.priority-low {
          border-left-color: #28a745;
        }

        .insight-icon {
          font-size: 16px;
          flex-shrink: 0;
        }

        .insight-content {
          flex: 1;

          .insight-title {
            font-weight: 500;
            font-size: 13px;
            color: #2c3e50;
            margin-bottom: 4px;
          }

          .insight-description {
            font-size: 12px;
            color: #6c757d;
            line-height: 1.4;
          }
        }
      }
    }
  }

  .learning-feedback {
    h4 {
      margin: 0 0 12px 0;
      font-size: 14px;
      font-weight: 600;
      color: #495057;
    }

    .feedback-form {
      .feedback-input {
        width: 100%;
        min-height: 80px;
        padding: 8px 12px;
        border: 1px solid #dee2e6;
        border-radius: 6px;
        resize: vertical;
        font-size: 12px;
        line-height: 1.4;
        margin-bottom: 8px;

        &:focus {
          outline: none;
          border-color: #007bff;
        }
      }

      .submit-btn {
        width: 100%;
        padding: 8px 12px;
        border: 1px solid #007bff;
        border-radius: 6px;
        background: #007bff;
        color: white;
        cursor: pointer;
        font-size: 12px;
        transition: all 0.2s;

        &:hover {
          background: #0056b3;
        }
      }
    }
  }
}

// 设置面板样式
.settings-panel {
  padding: 16px;
  height: 100%;
  overflow-y: auto;

  .settings-header {
    margin-bottom: 20px;

    h3 {
      margin: 0;
      font-size: 16px;
      font-weight: 600;
      color: #2c3e50;
    }
  }

  .settings-sections {
    .setting-section {
      margin-bottom: 24px;

      h4 {
        margin: 0 0 12px 0;
        font-size: 14px;
        font-weight: 600;
        color: #495057;
        border-bottom: 1px solid #e9ecef;
        padding-bottom: 6px;
      }

      .setting-item {
        margin-bottom: 12px;

        .setting-label {
          display: flex;
          align-items: center;
          gap: 8px;
          font-size: 13px;
          color: #495057;
          cursor: pointer;

          input[type="checkbox"] {
            margin: 0;
          }

          select {
            margin-left: auto;
            padding: 4px 8px;
            border: 1px solid #dee2e6;
            border-radius: 4px;
            font-size: 12px;
          }
        }
      }
    }
  }

  .settings-actions {
    display: flex;
    gap: 8px;
    margin-top: 20px;

    .reset-btn,
    .save-btn {
      flex: 1;
      padding: 8px 12px;
      border: 1px solid #dee2e6;
      border-radius: 6px;
      cursor: pointer;
      font-size: 12px;
      text-align: center;
      transition: all 0.2s;
    }

    .reset-btn {
      background: white;
      color: #6c757d;

      &:hover {
        background: #f8f9fa;
      }
    }

    .save-btn {
      background: #28a745;
      color: white;
      border-color: #28a745;

      &:hover {
        background: #218838;
      }
    }
  }
}

// 空状态样式
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  min-height: 100vh; // 确保至少占满视口高度
  position: relative;
  
  .empty-content {
    text-align: center;
    color: #6c757d;
    padding: 20px; // 添加内边距提供更好的视觉效果

    .empty-icon {
      font-size: 48px;
      margin-bottom: 16px;
      display: block; // 确保图标独占一行
    }

    h3 {
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      color: #333;
    }

    p {
      margin: 8px 0 0 0;
      font-size: 14px;
      line-height: 1.5; // 改善文字行高
    }
  }
}

// 🆕 拖拽提示浮层样式
.file-drag-hint {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  pointer-events: none;

  .drag-hint-content {
    background: white;
    border-radius: 12px;
    padding: 32px;
    text-align: center;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    transform: scale(0.95);
    animation: dragHintShow 0.2s ease-out forwards;

    .drag-hint-icon {
      font-size: 48px;
      margin-bottom: 16px;
    }

    .drag-hint-text {
      font-size: 18px;
      font-weight: 600;
      color: #2c3e50;
      margin-bottom: 8px;
    }

    .drag-hint-session {
      font-size: 14px;
      color: #6c757d;
      padding: 6px 12px;
      background: #f8f9fa;
      border-radius: 6px;
      display: inline-block;
    }
  }
}

@keyframes dragHintShow {
  from {
    opacity: 0;
    transform: scale(0.9);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}
