.chat-input-area{width:100%;height:100%;position:relative}.chat-input-area .edit-chat-container{position:absolute;top:0;left:0;right:0;bottom:0;outline:none;padding:5px;line-height:26px;font-size:var(--im-font-size);text-align:left;overflow-y:auto}.chat-input-area .edit-chat-container[data-placeholder="继续提问..."]:after{content:attr(data-placeholder);color:#999;font-style:italic;pointer-events:none;position:absolute;top:28px;left:5px;z-index:1}.chat-input-area .edit-chat-container>div:before{content:" ";font-size:14px;position:absolute;top:0;left:0}.chat-input-area .edit-chat-container .chat-image{display:block;max-width:200px;max-height:100px;border:1px solid #e6e6e6;cursor:pointer}.chat-input-area .edit-chat-container .chat-file-container{max-width:65%;padding:10px;border:2px solid #587ff0;display:flex;background:rgba(238,238,238,.8);border-radius:10px}.chat-input-area .edit-chat-container .chat-file-container .file-position-left{display:flex;width:80px;justify-content:center;align-items:center}.chat-input-area .edit-chat-container .chat-file-container .file-position-left .el-icon-document{font-size:40px;text-align:center;color:#d42e07}.chat-input-area .edit-chat-container .chat-file-container .file-position-right{flex:1}.chat-input-area .edit-chat-container .chat-file-container .file-position-right .file-name{font-size:16px;font-weight:600;color:#66b1ff}.chat-input-area .edit-chat-container .chat-file-container .file-position-right .file-size{font-size:14px;font-weight:600}.chat-input-area .edit-chat-container .chat-at-user{color:blue;border-radius:3px}.chat-input-area .edit-chat-container .roocode-mention{display:inline-block;padding:2px 6px;margin:0 2px;background:rgba(0,0,0,0);color:#409eff;border:1px solid #d0d0d0;border-radius:3px;font-size:14px;cursor:pointer;user-select:none;transition:all .2s ease}.chat-input-area .edit-chat-container .roocode-mention:hover{border-color:#409eff;background:#f0f7ff}.chat-input-area .edit-chat-container .roocode-mention:active{background:#e6f2ff}.chat-input-area .edit-chat-container>div:nth-of-type(1):after{content:"请输入消息（按Ctrl+Enter或Shift+Enter键换行）";color:gray}.chat-input-area .edit-chat-container.not-empty>div:nth-of-type(1):after{content:none}.chat-input-area .drag-overlay{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.5);display:flex;justify-content:center;align-items:center;z-index:999}.chat-input-area .drag-overlay .drag-content{background-color:#fff;padding:20px;border-radius:10px;text-align:center;box-shadow:0 4px 12px rgba(0,0,0,.15)}.chat-input-area .drag-overlay .drag-content i{font-size:40px;color:#587ff0;margin-bottom:10px;display:block}.chat-input-area .drag-overlay .drag-content p{margin:5px 0;font-size:16px;color:#333}.chat-input-area .drag-overlay .drag-content .drag-tip{font-size:14px;color:#666;margin-top:8px}