
.login-view {
	width: 100%;
	height: 100%;
	background: #E8F2FF;
	background-size: cover;
	box-sizing: border-box;

	// 自动登录加载状态样式
	.auto-login-loading {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #E8F2FF;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 9999;

		.loading-content {
			background: white;
			padding: 40px;
			border-radius: 10px;
			box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
			text-align: center;
			min-width: 300px;

			.loading-spinner {
				margin-bottom: 20px;

				i {
					font-size: 48px;
					color: #409EFF;
					animation: rotating 2s linear infinite;
				}
			}

			.loading-text {
				font-size: 18px;
				font-weight: 600;
				color: #333;
				margin-bottom: 10px;
			}

			.loading-phone {
				font-size: 14px;
				color: #666;
			}
		}
	}

	// 旋转动画
	@keyframes rotating {
		from {
			transform: rotate(0deg);
		}

		to {
			transform: rotate(360deg);
		}
	}

	.login-content {
		position: relative;
		display: flex;
		justify-content: space-around;
		align-items: center;
		padding: 10%;

		.login-form {
			width: 450px;
			padding: 30px;
			background: white;
			opacity: 0.95;
			box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
			border-radius: 10px;
			overflow: hidden;

			.login-brand {
				display: flex;
				justify-content: center;
				align-items: center;
				line-height: 50px;
				margin: 20px 0 30px 0;
				font-size: 22px;
				font-weight: 600;
				letter-spacing: 2px;
				text-transform: uppercase;
				text-align: center;

				.logo {
					width: 30px;
					height: 30px;
					margin-right: 10px;
				}
			}

			// 自定义Tab样式 - VSCode环境专用
			.custom-tabs {
				.custom-tabs-header {
					display: flex;
					border-bottom: 2px solid #DCDFE6;
					margin-bottom: 20px;
					
					.custom-tab-item {
						flex: 1;
						text-align: center;
						padding: 12px 0;
						font-size: 16px;
						font-weight: 500;
						color: #303133;
						cursor: pointer;
						position: relative;
						transition: all 0.3s;
						user-select: none;
						
						&:hover {
							color: #409EFF;
						}
						
						&.active {
							color: #409EFF;
							
							&::after {
								content: '';
								position: absolute;
								bottom: -2px;
								left: 0;
								right: 0;
								height: 2px;
								background-color: #409EFF;
							}
						}
					}
				}
				
				.custom-tabs-content {
					.custom-tab-pane {
						animation: fadeIn 0.3s;
					}
				}
			}
			
			// 原有的el-tabs样式
			.login-tabs {
				.el-tabs__header {
					margin: 0 0 20px 0;
				}

				.el-tabs__nav-wrap::after {
					height: 1px;
				}

				.el-tabs__item {
					font-size: 16px;
					font-weight: 500;
				}

				.el-tabs__content {
					padding-top: 10px;
				}
			}

			// 验证码行样式
			.verification-code-row {
				display: flex;
				align-items: center;
				gap: 10px;

				.code-input {
					flex: 1;
				}

				.send-code-btn {
					flex-shrink: 0;
					min-width: 100px;
					font-size: 12px;
				}
			}

			// 登录按钮样式
			.login-btn {
				width: 100%;
				height: 40px;
				font-size: 16px;
				font-weight: 500;
			}

			// 二维码容器样式
			.qrcode-container {
				display: flex;
				justify-content: center;
				align-items: center;
				min-height: 300px;

				.qrcode-box {
					text-align: center;

					.qrcode-loading,
					.qrcode-expired,
					.qrcode-scanned {
						display: flex;
						flex-direction: column;
						align-items: center;
						gap: 15px;

						i {
							font-size: 48px;
							color: #409EFF;
						}

						p {
							margin: 0;
							font-size: 14px;
							color: #666;
						}
					}

					.qrcode-active {
						.qrcode-image {
							margin-bottom: 15px;

							.mock-qrcode {
								width: 200px;
								height: 200px;
								border: 2px dashed #ddd;
								display: flex;
								flex-direction: column;
								justify-content: center;
								align-items: center;
								background: #f9f9f9;

								i {
									font-size: 48px;
									color: #999;
									margin-bottom: 10px;
								}

								p {
									margin: 0;
									color: #999;
									font-size: 14px;
								}
							}

							.qrcode-real {
								width: 200px;
								height: 200px;
								display: flex;
								justify-content: center;
								align-items: center;
								border: 1px solid #ddd;
								border-radius: 8px;
								overflow: hidden;

								img {
									width: 100%;
									height: 100%;
									object-fit: contain;
								}
							}
						}

						.qrcode-tip {
							margin: 0 0 10px 0;
							font-size: 14px;
							color: #666;
						}
					}

					.qrcode-expired {
						i {
							color: #F56C6C;
						}

						.auto-refresh-tip {
							margin: 10px 0;
							font-size: 12px;
							color: #909399;
							animation: blink 1.5s infinite;
						}

						.manual-refresh-btn {
							margin-top: 15px;
							color: #409EFF;
							font-size: 14px;
							padding: 8px 16px;

							&:hover {
								color: #66B1FF;
								background-color: rgba(64, 158, 255, 0.1);
							}

							i {
								color: inherit;
								margin-right: 5px;
								font-size: 14px;
							}
						}
					}

					.qrcode-scanned {
						i {
							color: #67C23A;
						}
					}
				}
			}

			// 绑定手机号容器样式
			.bind-phone-container {
				min-height: 300px;
				display: flex;
				flex-direction: column;
				justify-content: center;

				.bind-phone-header {
					text-align: center;
					margin-bottom: 30px;

					h3 {
						margin: 0 0 10px 0;
						font-size: 20px;
						font-weight: 600;
						color: #333;
					}

					p {
						margin: 0;
						font-size: 14px;
						color: #666;
					}
				}

				.cancel-btn {
					width: 100%;
					margin-top: 10px;
					color: #999;

					&:hover {
						color: #409EFF;
					}
				}
			}

			.register {
				display: flex;
				flex-direction: row-reverse;
				line-height: 40px;
				text-align: left;
				padding-left: 20px;
				margin-top: 20px;
			}
		}
	}
}

// 响应式设计
@media (max-width: 768px) {
	.login-view .login-content .login-form {
		width: 90%;
		padding: 20px;
	}

	// 自动登录加载状态的响应式设计
	.login-view .auto-login-loading .loading-content {
		width: 90%;
		padding: 30px 20px;
		min-width: auto;
	}
}

// 闪烁动画
@keyframes blink {
	0%,
	50% {
		opacity: 1;
	}

	51%,
	100% {
		opacity: 0.3;
	}
}

// 淡入动画
@keyframes fadeIn {
	from {
		opacity: 0;
		transform: translateY(10px);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}
