
.chat-group-member {
    display: flex;
    position: relative;
    padding: 0 5px;
    align-items: center;
    white-space: nowrap;
    box-sizing: border-box;

    .member-name {
        padding-left: 10px;
        height: 100%;
        text-align: left;
        white-space: nowrap;
        overflow: hidden;
        font-size: var(--im-font-size);
    }
}
