
.markdown-renderer {
  .markdown-body {
    // 复用 Roo-Code 的字体设置
    font-family: var(--vscode-font-family, -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif);
    font-size: var(--vscode-font-size, 13px);
    color: var(--vscode-editor-foreground, #333);
    word-wrap: break-word;
    
    // 紧凑的行高设置
    p, li, ol, ul {
      line-height: 1.15;
    }
    
    // 标题样式 - 更紧凑间距
    h1, h2, h3, h4, h5, h6 {
      margin-top: 0.5em;
      margin-bottom: 0.25em;
      font-weight: 600;
      line-height: 1.15;
    }
    
    // 首个元素无上边距
    > :first-child {
      margin-top: 0 !important;
    }
    
    // 最后一个元素无下边距
    > :last-child {
      margin-bottom: 0 !important;
    }
    
    h1 { font-size: 1.3em; border-bottom: 1px solid #e1e4e8; padding-bottom: 0.2em; }
    h2 { font-size: 1.2em; border-bottom: 1px solid #e1e4e8; padding-bottom: 0.15em; }
    h3 { font-size: 1.1em; }
    h4 { font-size: 1em; }
    h5 { font-size: 0.9em; }
    h6 { font-size: 0.85em; color: #6a737d; }
    
    // 段落 - 紧凑间距
    p { 
      white-space: pre-wrap;
      margin: 0.25em 0;
    }
    
    strong { font-weight: 600; }
    
    em { font-style: italic; }
    
    // 链接
    a {
      color: #0366d6;
      text-decoration: none;
      
      &:hover {
        text-decoration: underline;
      }
    }
    
    // 列表 - 紧凑间距
    ul, ol {
      padding-left: 2em;
      margin-left: 0;
      margin-top: 0.25em;
      margin-bottom: 0.25em;
    }
    
    ol {
      list-style-type: decimal;
    }
    
    ul {
      list-style-type: disc;
    }
    
    // 嵌套列表样式
    ul ul {
      list-style-type: circle;
      margin-top: 0;
      margin-bottom: 0;
    }
    
    ul ul ul {
      list-style-type: square;
    }
    
    ol ol {
      list-style-type: lower-alpha;
      margin-top: 0;
      margin-bottom: 0;
    }
    
    ol ol ol {
      list-style-type: lower-roman;
    }
    
    li {
      margin-top: 0.1em;
      margin-bottom: 0.1em;
      
      &.task-list-item {
        list-style: none;
        margin-left: -1.5em;
        
        .task-list-item-checkbox {
          margin-right: 0.5em;
        }
      }
    }
    
    // 引用 - 紧凑
    blockquote {
      padding: 0.25em 0.75em;
      color: #6a737d;
      border-left: 0.25em solid #dfe2e5;
      margin: 0.25em 0;
      
      > :first-child {
        margin-top: 0;
      }
      
      > :last-child {
        margin-bottom: 0;
      }
    }
    
    // 表格 - 紧凑样式
    .markdown-table {
      border-collapse: collapse;
      margin: 0.5em 0;
      width: auto;
      min-width: 50%;
      max-width: 100%;
      table-layout: fixed;
      border: 1px solid #cecece;
      
      th, td {
        border: 1px solid #cecece;
        padding: 8px 12px;
        text-align: left;
        word-wrap: break-word;
        overflow-wrap: break-word;
      }
      
      th {
        background-color: #f6f8fa;
        font-weight: 600;
      }
      
      tr:nth-child(even) {
        background-color: rgba(0, 0, 0, 0.02);
      }
      
      tr:hover {
        background-color: rgba(0, 0, 0, 0.04);
      }
    }
    
    // 行内代码 - 复用 Roo-Code 的样式
    code:not(pre code) {
      font-family: var(--vscode-editor-font-family, 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace);
      color: var(--vscode-textPreformat-foreground, #333) !important;
      background-color: var(--vscode-textPreformat-background, rgba(27, 31, 35, 0.05)) !important;
      padding: 0px 2px;
      white-space: pre-line;
      word-break: break-word;
      overflow-wrap: anywhere;
      border-radius: 3px;
      font-size: 85%;
    }
    
    // 代码块容器 - 紧凑布局
    .code-block-wrapper {
      margin: 8px 0;
      background: #f6f8fa;
      border-radius: 4px;
      overflow: hidden;
      
      .code-block-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 4px 12px;
        background: #f0f2f5;
        border-bottom: 1px solid #e1e4e8;
        
        .code-language {
          font-size: 12px;
          color: #586069;
          font-weight: 500;
        }
        
        .copy-button {
          padding: 4px 8px;
          font-size: 12px;
          color: #586069;
          background: white;
          border: 1px solid #d1d5da;
          border-radius: 3px;
          cursor: pointer;
          transition: all 0.2s;
          
          &:hover {
            background: #f3f4f6;
            border-color: #c6cbd1;
          }
          
          .copy-text { display: inline; }
          .copied-text { display: none; }
          
          &[data-copied="true"] {
            color: #28a745;
            border-color: #28a745;
            
            .copy-text { display: none; }
            .copied-text { display: inline; }
          }
        }
      }
      
      .code-block-content {
        pre {
          margin: 0;
          padding: 8px 12px;
          overflow-x: auto;
          background: #f6f8fa;
          
          code {
            font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
            font-size: 13px;
            line-height: 1.3;
            background: transparent;
            padding: 0;
          }
        }
      }
    }
    
    // 分隔线
    hr {
      height: 1px;
      padding: 0;
      margin: 0.5em 0;
      background-color: #e1e4e8;
      border: 0;
    }
    
    // 图片
    img {
      max-width: 100%;
      box-sizing: border-box;
      margin: 0.5em 0;
      display: block;
    }
    
    // 数学公式 - 紧凑布局
    .math-block {
      display: block;
      margin: 0.5em 0;
      text-align: center;
      padding: 0.5em;
      overflow-x: auto;
      overflow-y: hidden;
      background-color: rgba(27, 31, 35, 0.05);
      border-radius: 3px;
    }
    
    .katex {
      font-size: 1.1em;
      color: var(--vscode-editor-foreground, #333);
      font-family: KaTeX_Main, 'Times New Roman', serif;
      line-height: 1.2;
      white-space: normal;
      text-indent: 0;
    }
    
    .math-error {
      color: #d73a49;
      background: #ffeef0;
      padding: 2px 4px;
      border-radius: 3px;
    }
    
    // Mermaid 图表
    .mermaid-container {
      margin: 1em 0;
      text-align: center;
      
      .mermaid {
        display: inline-block;
        
        svg {
          max-width: 100%;
          height: auto;
        }
        
        &.mermaid-rendered {
          text-align: center;
        }
      }
      
      // Mermaid 降级显示
      .mermaid-fallback {
        background: #f8f9fa;
        border: 1px solid #dee2e6;
        border-radius: 4px;
        overflow: hidden;
        text-align: left;
        
        .mermaid-notice {
          display: flex;
          align-items: center;
          padding: 8px 12px;
          background: #e9ecef;
          border-bottom: 1px solid #dee2e6;
          
          .notice-icon {
            margin-right: 8px;
            font-size: 16px;
          }
          
          .notice-text {
            color: #495057;
            font-size: 12px;
            font-weight: 500;
          }
        }
        
        .mermaid-source {
          margin: 0;
          padding: 12px;
          background: #fff;
          color: #495057;
          font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
          font-size: 12px;
          line-height: 1.4;
          overflow-x: auto;
          white-space: pre;
        }
      }
      
      // 成功渲染的 Mermaid 图表
      .mermaid-rendered {
        text-align: center;
        
        svg {
          max-width: 100%;
          height: auto;
          display: inline-block;
        }
      }
      
      // Mermaid 图表通用样式
      .mermaid {
        text-align: center;
        margin: 1em 0;
        
        // 未处理时隐藏原始文本
        &:not([data-processed="true"]) {
          display: none;
        }
        
        // 已处理的图表
        &[data-processed="true"] {
          display: block;
        }
      }
    }
  }
}

// 暗色主题支持
@media (prefers-color-scheme: dark) {
  .markdown-renderer .markdown-body {
    color: #c9d1d9;
    
    h1, h2 {
      border-bottom-color: #30363d;
    }
    
    h6 {
      color: #8b949e;
    }
    
    a {
      color: #58a6ff;
    }
    
    blockquote {
      color: #8b949e;
      border-left-color: #30363d;
    }
    
    .markdown-table {
      th, td {
        border-color: #30363d;
      }
      
      th {
        background-color: #161b22;
      }
      
      tr:nth-child(2n) {
        background-color: #0d1117;
      }
    }
    
    code:not(pre code) {
      background-color: rgba(110, 118, 129, 0.4);
    }
    
    .code-block-wrapper {
      background: #161b22;
      
      .code-block-header {
        background: #0d1117;
        border-bottom-color: #30363d;
        
        .code-language {
          color: #8b949e;
        }
        
        .copy-button {
          color: #8b949e;
          background: #161b22;
          border-color: #30363d;
          
          &:hover {
            background: #1c2128;
            border-color: #48545d;
          }
        }
      }
      
      .code-block-content pre {
        background: #161b22;
      }
    }
    
    hr {
      background-color: #30363d;
    }
  }
}
