
.chat-agent-member {
  position: relative;
  display: flex;
  align-items: flex-start;
  padding: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 12px;
  border: 2px solid transparent;
  background: linear-gradient(white, white) padding-box,
              linear-gradient(45deg, #f0f2f5, #e6e8ec) border-box;
  margin-bottom: 12px;
  
  &:hover {
    background: linear-gradient(#f8fafb, #f8fafb) padding-box,
                linear-gradient(45deg, #1890ff, #36cfc9) border-box;
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(24, 144, 255, 0.12);
  }
  
  &.active {
    background: linear-gradient(#e6f7ff, #e6f7ff) padding-box,
                linear-gradient(45deg, #1890ff, #40a9ff) border-box;
    transform: translateY(-1px);
    box-shadow: 0 6px 16px rgba(24, 144, 255, 0.15);
  }
  
  &.disabled {
    opacity: 0.6;
    pointer-events: none;
  }
  
  .agent-avatar {
    margin-right: 16px;
    flex-shrink: 0;
    
    .avatar-container {
      position: relative;
      width: 52px;
      height: 52px;
      
      .status-indicators {
        position: absolute;
        bottom: -2px;
        right: -2px;
        display: flex;
        flex-direction: column;
        gap: 2px;
        
        .status-primary {
          width: 16px;
          height: 16px;
          border-radius: 50%;
          border: 3px solid white;
          box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
          
          &.status-online {
            background: linear-gradient(45deg, #52c41a, #73d13d);
          }
          
          &.status-offline {
            background: linear-gradient(45deg, #d9d9d9, #bfbfbf);
          }
          
          &.status-inactive {
            background: linear-gradient(45deg, #ff4d4f, #ff7875);
          }
        }
        
        .status-deployment {
          width: 10px;
          height: 10px;
          border-radius: 2px;
          border: 2px solid white;
          
          &.deployment-pc {
            background: #1890ff;
          }
          
          &.deployment-cloud {
            background: #722ed1;
          }
          
          &.deployment-web {
            background: #13c2c2;
          }
          
          &.deployment-mobile {
            background: #fa8c16;
          }
        }
      }
    }
  }
  
  .agent-info {
    flex: 1;
    min-width: 0;
    
    .agent-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 8px;
      
      .agent-name {
        font-weight: 600;
        font-size: 16px;
        color: #262626;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 150px;
      }
      
      .agent-badges {
        display: flex;
        gap: 4px;
        flex-shrink: 0;
      }
    }
    
    .agent-description {
      font-size: 13px;
      color: #595959;
      line-height: 1.4;
      margin-bottom: 12px;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      word-break: break-word;
    }
    
    .agent-meta {
      display: flex;
      flex-wrap: wrap;
      gap: 12px;
      margin-bottom: 8px;
      
      .meta-item {
        display: flex;
        align-items: center;
        gap: 4px;
        font-size: 12px;
        color: #8c8c8c;
        
        i {
          font-size: 14px;
          color: #bfbfbf;
        }
      }
    }
    
    .agent-capabilities {
      display: flex;
      flex-wrap: wrap;
      gap: 4px;
      
      .el-tag {
        font-size: 11px;
        height: 20px;
        line-height: 18px;
        border-radius: 10px;
      }
    }
  }
  
  .agent-actions {
    display: flex;
    flex-direction: column;
    gap: 8px;
    transition: all 0.3s ease;
    margin-left: 12px;
    
    .el-button--primary {
      background: linear-gradient(45deg, #1890ff, #40a9ff);
      border: none;
      box-shadow: 0 2px 8px rgba(24, 144, 255, 0.3);
      
      &:hover {
        transform: scale(1.05);
        box-shadow: 0 4px 12px rgba(24, 144, 255, 0.4);
      }
    }
    
    .el-button--text {
      width: 32px;
      height: 32px;
      border-radius: 50%;
    }
  }
  
  .heartbeat-indicator {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 8px;
    height: 8px;
    background: #52c41a;
    border-radius: 50%;
    
    &.pulse {
      animation: heartbeat 1s ease-in-out;
    }
  }
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
