
.home-page {
  height: 100vh;
  justify-content: center;
  align-items: center;

  overflow: hidden;
  background: var(--im-color-primary-light-9);
  background-color: #f6f6f6;
  //background-image: url('../assets/image/background.jpg');


  .navi-bar {
    --icon-font-size: 22px;
    width: var(--width);
    background: var(--im-color-primary-light-1);
    padding: 10px 8px;  /* 调整内边距适应水平布局 */

    .top{
      margin-left: auto;
    }

    .navi-bar-box {
      display: flex;

      justify-content: space-between;
      align-items: center;  /* 垂直居中 */

      .botoom {
        margin-bottom: 0;  /* 移除底部边距 */
      }
    }

    .menu {

      display: flex;

      justify-content: center;
      align-content: center;

      .menu-item {
        margin-bottom: 0;  /* 移除底部边距 */
        margin-right: 12px;  /* 改为右侧边距 */
      }
    }

    .user-head-image {
      display: flex;
      justify-content: center;
    }

    .menu {
      //margin-top: 10px;
      display: flex;

      justify-content: center;
      align-content: center;

      .link {
        text-decoration: none;
      }

      .router-link-active .menu-item {
        color: #fff;
        background: var(--im-color-primary-light-2);
      }

      .link:not(.router-link-active) .menu-item:hover {
        color: var(--im-color-primary-light-7);
      }

      .menu-item {
        position: relative;
        color: var(--im-color-primary-light-4);
        width: var(--width);

        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 12px;

        .icon {
          font-size: var(--icon-font-size);
        }

        .unread-text {
          position: absolute;
          background-color: var(--im-color-danger);
          left: 28px;
          top: 8px;
          color: white;
          border-radius: 30px;
          padding: 0 5px;
          font-size: var(--im-font-size-smaller);
          text-align: center;
          white-space: nowrap;
          border: 1px solid #f1e5e5;
        }
      }
    }

    .botoom-item {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 12px;
      width: 100%;
      padding: 5px;
      cursor: pointer;
      color: var(--im-color-primary-light-4);
      font-size: var(--icon-font-size);

      .icon {
        font-size: var(--icon-font-size);
      }

      &:hover {
        font-weight: 600;
        color: var(--im-color-primary-light-7);
      }
    }
  }

  .content-box {
    flex: 1;
    padding: 0;
    background-color: #fff;
    text-align: center;
  }
}
