
.chat-msg-item {
	.chat-msg-tip {
		line-height: 50px;
		font-size: var(--im-font-size-small);
		color: var(--im-text-color-light);
		text-align: center;
	}
	.chat-msg-normal {
		position: relative;
		font-size: 0;
		padding-left: 48px;
		min-height: 50px;
		margin-top: 10px;
		.head-image {
			position: absolute;
			width: 40px;
			height: 40px;
			top: 0;
			left: 0;
		}
		.chat-msg-content {
			text-align: left;
			.send-fail {
				color: #e60c0c;
				font-size: 30px;
				cursor: pointer;
				margin: 0 20px;
			}
			.chat-msg-top {
				display: flex;
				flex-wrap: nowrap;
				color: var(--im-text-color-light);
				font-size: var(--im-font-size);
				line-height: 20px;
				span {
					margin-right: 12px;
				}
			}
			.chat-msg-bottom {
				display: inline-block;
				padding-right: 300px;
				padding-left: 5px;
				.chat-msg-text {
					display: block;
					position: relative;
					line-height: 1.6;
					padding: 10px 14px;
					background-color: var(--im-background);
					border-radius: 10px;
					font-size: var(--im-font-size);
					text-align: left;
					
					// 确保文本可选择
					user-select: text !important;
					-webkit-user-select: text !important;
					-moz-user-select: text !important;
					-ms-user-select: text !important;
					
					&:after {
						content: "";
						position: absolute;
						left: -10px;
						top: 13px;
						width: 0;
						height: 0;
						border-style: solid dashed dashed;
						border-color: var(--im-background) transparent transparent;
						overflow: hidden;
						border-width: 10px;
					}
					
					// Markdown内容样式覆盖
					::v-deep {
						.unified-message-renderer {
							.message-markdown {
								.markdown-body {
									background: transparent !important;
									padding: 0 !important;
									
									// 调整代码块样式
									.code-block-wrapper {
										margin: 8px 0;
										
										.code-block-content {
											pre {
												background: rgba(0, 0, 0, 0.05);
												padding: 8px 12px;
											}
										}
									}
									
									// 调整段落间距
									p {
										margin-top: 0;
										margin-bottom: 8px;
										
										&:last-child {
											margin-bottom: 0;
										}
									}
									
									// 列表样式
									ul, ol {
										margin: 8px 0;
										padding-left: 20px;
									}
									
									// 标题样式
									h1, h2, h3, h4, h5, h6 {
										margin-top: 12px;
										margin-bottom: 8px;
										
										&:first-child {
											margin-top: 0;
										}
									}
								}
							}
						}
					}
				}
			}
		}
		&.chat-msg-mine {
			text-align: right;
			padding-left: 0;
			padding-right: 48px;
			.head-image {
				left: auto;
				right: 0;
			}
			.chat-msg-content {
				text-align: right;
				.chat-msg-top {
					flex-direction: row-reverse;
					span {
						margin-left: 12px;
						margin-right: 0;
					}
				}
				.chat-msg-bottom {
					padding-left: 180px;
					padding-right: 5px;
					.chat-msg-text {
						margin-left: 10px;
						background-color: var(--im-color-primary-light-2);
						color: #fff;
						&:after {
							left: auto;
							right: -10px;
							border-top-color: var(--im-color-primary-light-2);
						}
					}
				}
			}
		}
	}
}
