
.streaming-renderer {
  position: relative;
  
  // 加载占位符
  .loading-placeholder {
    display: inline-block;
    padding: 4px 0;
  }
  
  // 流式指示器
  .streaming-indicator {
    display: inline-flex;
    align-items: center;
    margin-top: 8px;
    color: #666;
    font-size: 12px;
    
    .streaming-dot {
      display: inline-block;
      width: 8px;
      height: 8px;
      background: #0066cc;
      border-radius: 50%;
      margin-right: 6px;
      animation: streaming-pulse 1.5s ease-in-out infinite;
    }
    
    .streaming-text {
      color: #999;
    }
  }
  
  @keyframes streaming-pulse {
    0%, 100% {
      opacity: 0.3;
      transform: scale(0.8);
    }
    50% {
      opacity: 1;
      transform: scale(1.2);
    }
  }
}

// 暗色主题
@media (prefers-color-scheme: dark) {
  .streaming-renderer {
    .streaming-indicator {
      color: #aaa;
      
      .streaming-text {
        color: #666;
      }
      
      .streaming-dot {
        background: #58a6ff;
      }
    }
  }
}
