  background-color: #f0f9ff;
  border-color: #3b82f6;
}

.terminal-item.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.terminal-item.current {
  background-color: #fff7ed;
  border-color: #f59e0b;
}

.terminal-icon {
  font-size: 24px;
  margin-right: 12px;
}

.terminal-info {
  flex: 1;
}

