  font-weight: 500;
  color: #1f2937;
}

.terminal-status {
  font-size: 12px;
  margin-top: 2px;
}

.terminal-status.online {
  color: #10b981;
}

.terminal-status.offline {
  color: #ef4444;
}

.terminal-status.current {
  color: #f59e0b;
}

.current-badge {
  position: absolute;
  top: 8px;
  right: 8px;
  padding: 2px 6px;
  font-size: 10px;
  background-color: #f59e0b;
  color: white;
  border-radius: 10px;
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #909399;
}

.empty-icon {
  font-size: 48px;
  margin-bottom: 16px;
}

.empty-text {
  font-size: 16px;
  margin-bottom: 8px;
}

.empty-hint {
  font-size: 12px;
