
.chat-page {
  height: 100% !important;
  
  // 重写Element UI的el-container样式
  
  
  .chat-list-box {
    height: 100% !important;
    
    // 重写Element UI的el-aside样式
    &.el-aside {
      height: 100% !important;
    }
    
    display: flex;
    flex-direction: column;
    background: var(--im-background);

    .chat-list-header {
      height: 50px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      padding: 0 2px 0 8px;

      .search-text {
        flex: 1;
        margin-right: 8px;
      }

      .new-terminal-chat-btn {
        padding: 4px 8px;
        font-size: 12px;
        border-radius: 12px;
        background: linear-gradient(135deg, #6c5ce7 0%, #a29bfe 100%);
        color: white;
        border: none;
        transition: all 0.2s ease;
        flex: 0 0 auto;
        min-width: 50px;

        &:hover {
          background: linear-gradient(135deg, #5f4fcf 0%, #9088e8 100%);
          transform: translateY(-1px);
          box-shadow: 0 2px 8px rgba(108, 92, 231, 0.3);
        }

        &:active {
          transform: translateY(0);
        }

        &:focus {
          outline: none;
          box-shadow: 0 0 0 2px rgba(108, 92, 231, 0.2);
        }
      }
    }

    .chat-list-loading {
      height: 50px;
      flex-shrink: 0;
      background-color: #eee;

      .el-icon-loading {
        font-size: 24px;
        color: var(--im-text-color-light);
      }

      .el-loading-text {
        color: var(--im-text-color-light);
      }

      .chat-loading-box {
        height: 100%;
      }
    }

    .chat-list-items {
      flex: 1;
      height: 0; // 强制flex子元素高度计算
    }
  }
}
