
.unified-message-renderer {
  // 使用CSS变量保持主题一致性
  --msg-text-color: var(--im-text-primary, #333);
  --msg-bg-color: var(--im-bg-primary, #fff);
  --msg-link-color: var(--im-link-color, #0066cc);
  --msg-code-bg: var(--im-code-bg, #f6f8fa);
  --msg-code-color: var(--im-code-color, #032f62);
  --msg-border-color: var(--im-border-color, #e1e4e8);
  
  color: var(--msg-text-color);
  font-size: 14px;
  line-height: 1.6;
  word-break: break-word;
  
  // 纯文本样式
  .message-text {
    white-space: pre-wrap;
    
    ::v-deep a {
      color: var(--msg-link-color);
      text-decoration: none;
      
      &:hover {
        text-decoration: underline;
      }
    }
  }
  
  // Markdown 容器样式
  .message-markdown {
    // 样式将在 MarkdownRenderer 组件中定义
  }
  
  // 增强功能区域
  .message-enhancements {
    margin-top: 8px;
    padding-top: 8px;
    border-top: 1px solid var(--msg-border-color);
  }
}

// 暗色主题支持
@media (prefers-color-scheme: dark) {
  .unified-message-renderer {
    --msg-text-color: #e1e4e8;
    --msg-bg-color: #0d1117;
    --msg-link-color: #58a6ff;
    --msg-code-bg: #161b22;
    --msg-code-color: #79c0ff;
    --msg-border-color: #30363d;
  }
}
