
.roocode-at-panel {
  position: fixed;
  background: white;
  border: 1px solid #e4e7ed;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  z-index: 3000;
  min-width: 280px;
  max-width: 400px;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  .panel-header {
    padding: 12px 16px;
    border-bottom: 1px solid #e4e7ed;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 8px;

    .back-btn {
      cursor: pointer;
      color: #606266;
      &:hover {
        color: #409EFF;
      }
    }
  }

  .panel-search {
    padding: 8px 16px;
    border-bottom: 1px solid #e4e7ed;

    .search-input {
      width: 100%;
      padding: 6px 10px;
      border: 1px solid #dcdfe6;
      border-radius: 4px;
      font-size: 14px;
      outline: none;

      &:focus {
        border-color: #409EFF;
      }
    }
  }

  .panel-items {
    flex: 1;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 8px 0;
    max-height: 300px;
    min-height: 100px;
    
    // 美化滚动条
    &::-webkit-scrollbar {
      width: 6px;
    }
    
    &::-webkit-scrollbar-track {
      background: #f1f1f1;
      border-radius: 3px;
    }
    
    &::-webkit-scrollbar-thumb {
      background: #888;
      border-radius: 3px;
      
      &:hover {
        background: #555;
      }
    }

    .panel-item {
      padding: 10px 16px;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: 10px;
      transition: background-color 0.2s;
      width: 100%;
      box-sizing: border-box;
      text-align: left;

      &:hover, &.active {
        background-color: #f5f7fa;
      }

      &.active {
        background-color: #ecf5ff;
      }
      
      &.selected {
        background-color: #f0f9ff;
      }
      
      .selected-mark {
        color: #67c23a;
        font-size: 16px;
        margin-left: 8px;
        flex-shrink: 0;
      }

      i {
        font-size: 18px;
        color: #909399;
        flex-shrink: 0;
      }

      .item-icon {
        font-size: 18px;
        width: 20px;
        text-align: center;
        flex-shrink: 0;
      }

      .item-content {
        flex: 1;
        min-width: 0;
        text-align: left;

        .item-name {
          font-size: 14px;
          color: #303133;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          text-align: left;
        }

        .item-desc {
          font-size: 12px;
          color: #909399;
          margin-top: 2px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          text-align: left;
        }
      }

      .item-type {
        font-size: 12px;
        color: #909399;
        padding: 2px 6px;
        background: #f4f4f5;
        border-radius: 2px;
        margin-left: auto;
        margin-right: 0;
        flex-shrink: 0;
      }

      .el-icon-arrow-right {
        margin-left: auto;
        color: #c0c4cc;
      }
    }

    .loading, .empty {
      padding: 20px;
      text-align: center;
      color: #909399;
      font-size: 14px;
    }
  }
}
